// Core libraries
import React, { useState } from 'react';

// External libs and components
import { createStyles, makeStyles } from '@material-ui/core/styles';
import {
  MapContainer,
  TileLayer,
} from 'react-leaflet';
import { LatLng } from 'leaflet';


// Internal libs
import config from 'app/config';
import { CompanyLocations } from 'model/CompanyLocations';
import { ZipCode } from 'model/ZipCode';

// Internal components
import ZipCodeMarkers from './ZipCodeMarkers';
import ZipCodesRadius from './ZipCodesRadius';

// Styles hook
const useStyles = makeStyles((theme) =>
  createStyles({
    root: {},
    mapContainer: {
      height: '100vh',
    width: '65vw'
    },
  }),
);

// Props type
type ZipCodesMapProps = {
  zipCodes: ZipCode[];
  extraZipCodes: ZipCode[];
  lat: number;
  lng: number;
  radius: number;
  initZoom?: number;
  initCenter?: LatLng | [number, number];
  showMarkers: boolean;
  showAreas: boolean;
};

// Component
const ZipCodesMap = ({
    zipCodes,
    extraZipCodes,
    lat,
    lng,
    radius,
    initZoom = config.mapZoom,
    initCenter = config.mapCenter,
    showAreas,
    showMarkers,
}: ZipCodesMapProps) => {
  const styles = useStyles();

  const [center] = useState(initCenter);
  const [zoom] = useState(initZoom);

  return (
      <MapContainer center={center} zoom={zoom} className={styles.mapContainer}>
        {/* @ts-ignore */}
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <ZipCodesRadius lat={lat} lng={lng} radius={radius}/>
          <ZipCodeMarkers
              showMarkers={showMarkers}
              showAreas={showAreas}
              zipCodes={zipCodes} />
          <ZipCodeMarkers
              showMarkers={showMarkers}
              showAreas={showAreas}
              zipCodes={extraZipCodes} alternate={true} />
      </MapContainer>
  );
};

export default ZipCodesMap;
