// Core libraries
import React, { useMemo, useState } from 'react';

// External libs and components
import { makeStyles } from '@material-ui/core/styles';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

// Internal libs

// Internal components
import { CompanyLocations } from 'model/CompanyLocations';
import { ZipCode, ZipCodeFormData } from 'model/ZipCode';
import { InProgressState } from "model/InProgressState";

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column'
    },
    formControl: {
        margin: theme.spacing(3),
    },
}));

// Props type
type ZipCodeControlsProps = {
    showMarkers: boolean;
    setShowMarkers: (checked: boolean) => void;
    showAreas: boolean;
    setShowAreas: (checked: boolean) => void;
    formData: ZipCodeFormData;
    setFormData: (data: ZipCodeFormData) => void;
    submitFormData: () => void;
    dataState: InProgressState | undefined;
};
// Component
const ZipCodeControls = ({
    showMarkers,
    setShowMarkers,
    showAreas,
    setShowAreas,
    formData,
    setFormData,
    submitFormData,
    dataState
}: ZipCodeControlsProps) => {

    const handleChange = (event: any) => {
        setFormData({ ...formData, [event.target.name]: parseFloat(event.target.value) });

    };
    const classes = useStyles();
  return (
    <>
        <FormControl component="fieldset" className={classes.formControl}>
            <FormLabel component="legend">Dostępne warstwy</FormLabel>
            <FormGroup>
                <FormControlLabel
                    control={
                        <Checkbox checked={showMarkers}
                                  onChange={event => setShowMarkers(event.target.checked)}
                                  name="showMarkers" />
                    }
                    label="Centroidy kodów"
                />
                <FormControlLabel
                    control={
                        <Checkbox checked={showAreas}
                                  onChange={event => setShowAreas(event.target.checked)}
                                  name="showAreas" />
                    }
                    label="Obszary kodów"
                />
            </FormGroup>
        </FormControl>
        <FormControl component="fieldset" className={classes.formControl}>
            <FormLabel component="legend">Wyszukaj kody</FormLabel>
            <FormGroup>
                <form className={classes.root} noValidate autoComplete="off">
                    <TextField required
                               className={classes.formControl}
                               value={formData.lat} onChange={handleChange}
                               name="lat" type='number' label="Latitude" />
                    <TextField required
                               className={classes.formControl}
                               value={formData.lng} onChange={handleChange}
                               name="lng" type='number' label="Longitude" />
                    <TextField required
                               type='number'
                               className={classes.formControl}
                               value={formData.radius} onChange={handleChange}
                               name="radius" label="Promień" defaultValue="5" />
                    <Button variant="contained" color="primary" onClick={submitFormData} disabled={dataState?.inProgress}>
                        Wyszukaj
                    </Button>
                </form>
            </FormGroup>
        </FormControl>
    </>
  );
};

export default ZipCodeControls;
