import { ZipCodesResponse, ZipCode } from "../model/ZipCode";
import axios from 'axios';

export const getZipCodes = async (
  lat: number,
  lng: number,
  radius: number,
): Promise<ZipCodesResponse> => {
  const response = await axios.get(
      '/region/zip-codes',
      {
          params: {
              lat,
              lng,
              radius
          }
      }
  );

  return response?.data;
};
