// Core libraries
import React, { useCallback, useEffect, useState } from 'react';

// External libs and components
import { createStyles, makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CircularProgress from '@material-ui/core/CircularProgress'

// Internal libs
import config from 'app/config';

// Internal components
import { ReactComponent as Logo } from 'assets/xd_logo_long.svg';
import ZipCodesMap from './components/ZipCodesMap';
import ZipCodeControls from './components/ZipCodeControls';
import { InProgressState } from '../../model/InProgressState';
import { ZipCode, ZipCodesResponse } from '../../model/ZipCode';
import * as api from '../../api';


const settings = {
    name: 'CCC 444 Bydgoszcz Focus Mall',
    lat: 53.124578,
    lng: 18.018055,
    radius: 5,
};
// Component
const ZipCodesPage = () => {
  const styles = useStyles();

  const [getDataState, setGetDataState] = useState<InProgressState>({
    inProgress: false,
  });
  const [zipCodes, setZipCodes] =
    useState<ZipCodesResponse | undefined>(undefined);

    const [locationForm, setLocationForm] = useState<{
        lat: number;
        lng: number;
        radius: number;
    }>({
        lat: 53.124578,
        lng: 18.018055,
        radius: 5,
    });

  const [showMarkers, setShowMarkers] = useState<boolean>(true);
  const [showAreas, setShowAreas] = useState<boolean>(false);

  const fetchZipCodes = useCallback(async () => {
    setGetDataState({ inProgress: true });
    try {
        const results = await api.getZipCodes(
            locationForm.lat,
            locationForm.lng,
            locationForm.radius,
        );
        setZipCodes(results);
        setGetDataState({ inProgress: false, success: true });
    } catch (error) {
      setGetDataState({ inProgress: false, success: false, error });
    }
  }, [locationForm]);

  useEffect(() => {
    fetchZipCodes();
  }, []);

  return (
    <Container className={styles.root} maxWidth={false} disableGutters>
      <div className={styles.topBar}>
        <Logo className={styles.logo} title={'Xpress →Delivery'} />
      </div>
        <div className={styles.mainContainer}>
            <div className={styles.leftPaneContainers}>
                <ZipCodeControls showMarkers={showMarkers} setShowMarkers={setShowMarkers}
                                 showAreas={showAreas} setShowAreas={setShowAreas}
                                 formData={locationForm} setFormData={setLocationForm}
                                 submitFormData={fetchZipCodes}
                                 dataState={getDataState}
                />
                <div className={styles.message}>
                    Kody:{' '}
                    {getDataState.inProgress && <CircularProgress size={16} thickness={6}/>}
                    {getDataState.error && "wystąpił problem podczas wczytywania listy miejsc"}
                    {zipCodes &&

                    zipCodes.zipCodes.map((zipCode, idx) => {
                        const last = idx === zipCodes.zipCodes.length - 1;
                        return (
                            <React.Fragment key={idx}>
                                <span>{zipCode.name}</span>
                                {!last && ",\n"}
                            </React.Fragment>
                        );
                    })}
                </div>
            </div>
            <div className={styles.mapContainer}>
                <ZipCodesMap
                    showMarkers={showMarkers}
                    showAreas={showAreas}
                    zipCodes={zipCodes?.zipCodes || []}
                    lat={locationForm.lat}
                    lng={locationForm.lng}
                    extraZipCodes={zipCodes?.zipCodesOutOfBounds || []}
                    radius={locationForm.radius || 0}
                />
            </div>
        </div>
      <div className={styles.footer}>
        <span>© Xpress →Delivery</span>
      </div>
    </Container>
  );
};
// Styles hook
const useStyles = makeStyles((theme) =>
  createStyles({
      mainContainer: {
        display: 'flex',
        flexDirection:'row'
      },
      leftPaneContainers: {
          display: 'flex',
          flexDirection: 'column',
      },
    root: {
      padding: 20,
      display: 'flex',
      flexDirection: 'column',
      height: '100vh',
    },
    topBar: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'center',
      marginTop: theme.spacing(3),
      marginBottom: theme.spacing(2),
    },
    message: {
      marginBottom: theme.spacing(2),
    },
    logo: {
      height: 'auto',
      width: (32 * 600) / 58,
      maxWidth: '90%',
    },
    mapContainer: {
      flexGrow: 1,
    },
    footer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      padding: theme.spacing(2),
    },
  }),
);

export default ZipCodesPage;
